<template>
  <p class="blank"></p>
  <coarousel :carousel="carousel" :interval='interval'></coarousel>
  <div class="row">
    <div class="col-xs-9">
      <movieList></movieList>
    </div>
    <div class="col-xs-3">
      <hot></hot>
    </div>
  </div>
  <div class="row">
    <div class="col-xs-9">
      <review></review>
    </div>
    <div class="col-xs-3">
      <expectation></expectation>
    </div>
  </div>
</template>

<script>
import coarousel from "@/components/index/carousel.vue";
import movieList from "@/components/index/movieList.vue";
import hot from "@/components/index/hot.vue";
import review from "@/components/index/review.vue";
import expectation from "@/components/index/expectation.vue";
export default {
  components:{
    coarousel,
    movieList,
    hot,
    review,
    expectation,
  },
  data(){
    return{
      carousel:[],
      // carousel:[
      //   {searchValue: null, createBy: "admin", createTime: "2021-05-05 12:20:57", updateBy: "admin", updateTime: "2021-09-17 11:05:36"},
      //   {searchValue: null, createBy: "admin", createTime: "2021-05-05 12:25:58", updateBy: "1", updateTime: "2021-05-05 12:29:09"},
      //   {searchValue: null, createBy: "admin", createTime: "2021-05-05 12:28:53", updateBy: null, updateTime: null},
      // ],
      interval:2000,
    }
  },
  created(){
    // console.log(this.carousel);
    this.axios({
      method:"get",
      url:"/prod-api/api/movie/rotation/list",
    })
    .then((resopnse)=>{
      if(resopnse.status == 200){
        // console.log(resopnse.data.rows)
       this.carousel = resopnse.data.rows;
      }
    })
    .catch((error)=>{
      console.log(error)
    });
    this.axios({
      method:"get",
      url:"/prod-api/api/movie/film/list",
    })
    .then((resopnse)=>{
      if(resopnse.status == 200){
        if(resopnse.data.code == 200){
          // console.log(resopnse.data)
          this.movieList = resopnse.data.rows
          // console.log(this.movieList)
        }else{
          
        }
      }
    })
    .catch((error)=>{
      console.log(error)
    })
  }
}
</script>
