<template>
  <div class="row latest">
    <div class="col-xs-12">
      <div class="lead">
        <i class="glyphicon glyphicon-list"></i>
        <strong>影片列表</strong>
        <h5 class="pull-right">
          <router-link to="/movieList"><strong>更多</strong><span class="glyphicon glyphicon-chevron-right"></span></router-link>
        </h5>
      </div>
    </div>
    <div class="tab-content col-xs-12">
      <div class="row">
        <div class="col-lg-3 col-md-3 col-xs-4" v-for='i in movieList.slice(1,9)' :key="i">
          <router-link :to="{ name: 'details', params: { id: i.id } }">
            <div class="thumbnail">
              <img :src="this.url + i.cover" alt="">
              <div class="caption">
                <h4>
                  <span class="pull-left">{{i.name}}</span>
                  <span class="text-warning pull-right">
                    {{i.score}}分
                  </span>
                </h4>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {

  data(){
    return{
      movieList:[],
      url:'http://124.93.196.45:10001',
    }
  },
  created(){
    this.axios({
        method:"get",
        url:"/prod-api/api/movie/film/list",
      })
      .then((resopnse)=>{
        if(resopnse.status == 200){
          if(resopnse.data.code == 200){
            // console.log(resopnse.data)
            this.movieList = resopnse.data.rows
            // console.log(this.movieList)
          }else{
            
          }
        }
      })
      .catch((error)=>{
        console.log(error)
      })
  }
}
</script>

<style scoped>
.lead a{
  color: black;
  text-decoration: none;
}
.thumbnail{
  transition: all 0.3s;
}
.thumbnail:hover{
  box-shadow: 0px 0px 40px #d2c4c4;
}
.thumbnail img{
  height: 200px;
}
.thumbnail h4{
  height: 20px;
}
.thumbnail h4 span:nth-child(1){
  margin: 0px 0px;
  width: 120px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.text-warning{
  color: #ffb400;
}
</style>