<template>
  <div class="row latest">
    <div class="col-xs-12">
      <p class="lead">
        <i class="glyphicon glyphicon-signal"></i><strong>期待影片</strong>
      </p>
    </div>
    <div class="tab-content col-xs-12">
      <div class="row">
        <div class="" v-for='(i,index) in likeList1.slice(0,10)' :key="index">
          <router-link :to="{ name: 'details', params: { id: i.id } }">
            <h4>
              <span class="ranking"> <em>{{index+1}}</em> </span>
              <span>{{i.name}}</span>
              <span class="text-danger pull-right">
                {{i.likeNum}}
              </span>
            </h4>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      likeList:[],
      url:'http://124.93.196.45:10001',
    }
  },
  mounted(){
    
  },
  computed:{
      likeList1:function(){
          return this.likeList.sort((a,b)=>{
            return b.likeNum-a.likeNum // 降序
          })
      }
  },

  created(){
    this.axios({
      method:"get",
      url:"/prod-api/api/movie/film/list",
      params: {recommend:"Y"}
    })
    .then((resopnse)=>{
      if(resopnse.status == 200){
        if(resopnse.data.code == 200){
          // console.log(resopnse.data)
          this.likeList = resopnse.data.rows
          // console.log(this.likeList)
        }else{
          
        }
      }
    })
    .catch((error)=>{
      console.log(error)
    })
  },
}
</script>

<style scoped>
.ranking{
  padding: 0px 10px;
}
a{
  display: block;
  text-decoration: none;
  color: #333;
  height: 40px;
}
a h4{
  line-height: 40px;

}
a:hover{
  background: #e6e4e4;
}
.latest{
  background: #fff;
}
.tab-content h4 span{
  display: block;
  float: left;
}
.tab-content h4 span:nth-child(2){
  margin: 0px 0px;
  width: 120px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>