<template>
  <div class="row latest">
    <div class="col-xs-12">
      <div class="lead">
        <i class="glyphicon glyphicon-pencil"></i>
        <strong>相关影评</strong>
        <h5 class="pull-right">
          <router-link to="/news"><strong>更多</strong><span class="glyphicon glyphicon-chevron-right"></span></router-link>
        </h5>
      </div>
    </div>
    <div class="tab-content col-xs-12">
      <div class="row">
        <div class="col-xs-6" v-for='i in reviewList.slice(30,34)' :key="i">
          <router-link :to="{ name: 'newsdetail', params: { id: i.id } }">
            <div class="thumbnail">
              <div class="img">
                <img :src="this.url + i.cover" alt="">
              </div>
            
            <div class="caption">
              <h4>
                {{i.name}}
                <p>
                  {{i.title}}
                </p>
              </h4>
              <div class="text-muted">
                <span class="text-muted text-left">
                  猫眼电影
                </span>
                <p class="text-muted pull-right">
                  <i class="glyphicon glyphicon-eye-open"></i>
                  <span class="">
                    {{i.readNum}}
                  </span>
                </p>
              </div>
            </div>
          </div>
          </router-link>
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      reviewList:[],
      url:'http://124.93.196.45:10001',
    }
  },
  created(){
    this.axios({
        method:"get",
        url:"/prod-api/api/movie/press/press/list",
      })
      .then((resopnse)=>{
        if(resopnse.status == 200){
          if(resopnse.data.code == 200){
            // console.log(resopnse.data)
            this.reviewList = resopnse.data.rows
            console.log(this.reviewList)
          }else{
            
          }
        }
      })
      .catch((error)=>{
        console.log(error)
      })
  }
}
</script>

<style scoped>
a{
  text-decoration: none;
}
.thumbnail{
  transition: all 0.3s;
}
.thumbnail:hover{
  box-shadow: 0px 0px 40px #d2c4c4;
}
.thumbnail{
  width:100%;
  height: 100%;
}
.lead a{
  color: black;
  text-decoration: none;
}
.thumbnail .img{
  height: 280px;
  overflow: hidden;
}
.thumbnail img{
  /* width: 100%; */
  height: 100%;
}
.text-warning{
  color: #ffb400;
}
</style>