<template>
  <div class="container" @mouseout="autoRun" @mouseover="autostop">
    <ul class="img"
    :style="{width:carousel.length*100+'%',marginLeft:-listIndex*100+'%',}">
      <li v-for='(i,index) in carousel' :key="index"
      :class="{ active: index == listIndex }">
        <!-- <router-link :to="/book/+i.id">
          
        </router-link> -->
        <a :href="i.targetId">
          <img :src="this.url + i.advImg" />
        {{i.targetId}}
        </a>
      </li>
    </ul>
    <ul class="page">
      <li v-for="(i,index) in carousel" :key="index"
      :class="{ active: index == listIndex }"
      @click="listIndex = index">
      </li>
    </ul>
    <a href="javascript:void(0)" class="prev" @click='prePage'>&lt;</a>
    <!-- javascript:void(0) 避免超链接进行跳转 -->
    <a href="javascript:void(0)" class="next" @click='nextPage'>&gt;</a>
  </div>
</template>

<script>
export default {
  props:{
    carousel:{
      type:Array,
      required:true,
    },
    interval:{
      type:Number,
      default:2000,
    },
  },
  data(){
    return{
      listIndex: 0,
      timer: null,
      url:'http://124.93.196.45:10001',
    }
  },
  computed:{
  },
  methods:{
    autoRun(){//图片自动轮播
      if(this.timer){
        return;
      }
      this.timer = setInterval(() => {//定时器
        this.listIndex = (this.listIndex+1) % this.carousel.length;
      },this.interval)
    },
    autostop() {//移入时，清除定时器
      clearInterval(this.timer);
      this.timer=null; 
    },
    prePage(){//上一页
      this.listIndex--;
      if (this.listIndex == 0) {
        this.listIndex = this.carousel.length - 1;
      }
    },
    nextPage(){//下一页
      this.listIndex = (this.listIndex+1) % this.carousel.length;
    },
  },
  mounted(){
    this.autoRun();
  },
  deforeUnmount(){
    this.autostop()
  },
  updated(){
  }
}
</script>

<style scoped>
* {
  margin: 0px;
  padding: 0px;
  list-style-type: none;
  text-decoration: none;
  color: #000;
}
a:hover,a:active{
  text-decoration: none;
}

.container {
  width: 1170px;
  height: 500px;
  margin: 20px auto;
  position: relative;
  overflow: hidden;
}

.container > .img{
  width: 1170*3px;
  height: 500px;
  margin-left:0%;
  transition: margin-left linear 0.5s;
}

.container > .img li {
  float: left;
  width: 1170px;
  height: 100%;
  opacity: 0.6;
}
.container > .img li.active {
  opacity: 1;
  transition: opacity linear 0.5s;
}

.container > .img li > a > img {
  width: 100%;
  height: 100%;
}

.page {
  display: flex;
  /* justify-content: space-between;
  width: 80px; */
  width: 100%;
  position: absolute;
  bottom:0px;
  left:0px;
  /* bottom: 30px;
  left: 50%;
  margin-left: -40px; */
}

.page > li {
  background-color: #fff;
  width: 33.33%;
  height: 10px;
  line-height: 20px;
  /* text-align: center; */
  /* border-radius: 10px; */
}

.page > li.active {
  background-color: #d9534f;
  color: #fff;
}
.container > a {
  display: block;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.3);
  color: #fff;
  font-size: 20px;
  width: 50px;
  height: 490px;
  line-height: 490px;
  text-align: center;
  top:0px
}
a.prev {
  display: block;
  left: 0px;
}
a.next {
  display: block;
  right: 0px;
}
</style>